import React from 'react';
import Konva from 'konva';
import { Stage, Layer, Rect, Text, Circle, Line } from 'react-konva';
//import { Button } from './Button.js';
//import { Input } from './Input.js';
import './App.css';

class MainText extends React.Component {
  render() {
    return (
      <div className="main-text">
        <h1>
          Your color is: {this.props.color}.
        </h1>
      </div>
    );
  }
}

class RandomButtonColumn extends React.Component {
  
  handleClick(parameter, event) {
    let data = parameter;
    this.props.onGetColorComponent(data);
  }

  render() {
    const redComponent = this.props.red;
    const greenComponent = this.props.green;
    const blueComponent = this.props.blue;
    return (
      <div className="random-input">
        <p>
          Random:
        </p>
        <Button text="random red" onClick={this.handleClick.bind(this, 'red')} style={{backgroundColor: `rgb(${redComponent},0,0)`}} />
        <Button text="random green" onClick={this.handleClick.bind(this, 'green')} style={{backgroundColor: `rgb(0,${greenComponent},0)`}} />
        <Button text="random blue" onClick={this.handleClick.bind(this, 'blue')} style={{backgroundColor: `rgb(0,0,${blueComponent})`}} />
        <Button text="random alpha" onClick={this.handleClick.bind(this, 'alpha')} style={{backgroundColor: `#000000`}} />
      </div>
    );
  }
}

class Button extends React.Component {
	render() {
		return (
      <button onClick={this.props.onClick}
        className='button' 
				style={this.props.style}
				>
				{this.props.text}
			</button>
		);
    }
    
}

class ManualInputColumn extends React.Component {
  handleChange(parameter, event) {
    let component = parameter;
    let componentValue = event.target.value;
    this.props.onGetColorComponentAndValue(component, componentValue);
  }
  render() {
    return (
      <div className='manual-input'>
        <p>
          Manual input
        </p>
        <div>
          <Input value={this.props.value[0]} max="255" step="1" onChange={this.handleChange.bind(this, 'red')}/>
          <Input value={this.props.value[1]} max="255" step="1" onChange={this.handleChange.bind(this, 'green')}/>
          <Input value={this.props.value[2]} max="255" step="1" onChange={this.handleChange.bind(this, 'blue')}/>
          <Input value={this.props.value[3]} max="1" step="0.01" onChange={this.handleChange.bind(this, 'alpha')}/>
        </div>
      </div>
    );
  }
}

class Input extends React.Component {
  render() {
    return (
      <input
        type="number"
        size="10"
        min="0"
        max={this.props.max}
        step={this.props.step}
        value={this.props.value}
        onChange={this.props.onChange}
      />
    );
  }
}

class Shape extends React.Component {
  render() {
    return (
      <Stage width={window.innerWidth} height='200'>
      <Layer>
        <Circle x={window.innerWidth/2} y={125} radius={50} fill={this.props.color}/>
      </Layer>
    </Stage>
    );
  }
}

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      red: 10,
      green: 72,
      blue: 129,
      alpha: 1.0,
      test: ''
    };
    this.handleColorComponent = this.handleColorComponent.bind(this);
    this.handleColorComponentAndValue = this.handleColorComponentAndValue.bind(this);
  }
  componentDidMount() {
    this.applyColor();
  }

  componentDidUpdate(prevProps, prevState) {
    this.applyColor();
  }

  formatColor(r, g, b, a) {
    return `rgba(${r}, ${g}, ${b}, ${a})`;
  }

  isLight() {
    /*const rgb = +this.state.red + +this.state.green + +this.state.blue;
    return rgb < (127 * 3);*/
  }

  applyColor() {
    const color = this.formatColor(this.state.red, this.state.green, this.state.blue, this.state.alpha);
    const totalColorValue = this.state.red*299 + this.state.green*587 + this.state.blue*114;
    const invertedColor = this.formatColor((255-this.state.red), (255-this.state.green), (255-this.state.blue), this.state.alpha);
    /*if(totalColorValue >= 125000 || this.state.alpha >= 0.6) {
      document.body.style.background = this.formatColor(0,0,0,1);
    } else {
      document.body.style.background = this.formatColor(240,240,240,1);
    }*/
    //document.body.style.background = this.formatColor(0,0,0,1-this.state.alpha);
    //document.getElementsByClassName('main-text')[0].style.color = color;
    //document.getElementsByClassName('random-input')[0].style.color = color;
    //document.getElementsByClassName('manual-input')[0].style.color = color;
  }

  randomizeValue() {
    let random = 0;
    random = Math.floor(Math.random()*256);
    return random;
  }

  handleColorComponent(parameter) {
    if(parameter === 'alpha') {
      this.setState({
        [parameter]: (this.randomizeValue()/ 256).toFixed(2)
      });
    } else {
      this.setState({
        [parameter]: this.randomizeValue()
      });
    }
  }

  handleColorComponentAndValue(component, componentValue) {
    this.setState({
      [component]: componentValue
    });
  }

  render() {
    return (
      <div>
        <MainText 
          color={this.formatColor(this.state.red, this.state.green, this.state.blue, this.state.alpha)}
        />
        <RandomButtonColumn
          onGetColorComponent={this.handleColorComponent}
          red={this.state.red}
          green={this.state.green}
          blue={this.state.blue}
        />
        <ManualInputColumn onGetColorComponentAndValue={this.handleColorComponentAndValue} value={[this.state.red, this.state.green, this.state.blue, this.state.alpha]}/>
        <Shape
          color={this.formatColor(this.state.red, this.state.green, this.state.blue, this.state.alpha)}
          invertedColor={this.formatColor(255-this.state.red, 255-this.state.green, 255-this.state.blue, this.state.alpha)}
        />
      </div>
    );
  }
}

export default App;


